import {Injectable} from '@angular/core';
import {HttpEvent, HttpRequest, HttpHandler, HttpInterceptor, HttpResponse} from '@angular/common/http';
import {Observable, throwError} from 'rxjs';
import {tap, filter, catchError, takeUntil} from 'rxjs/operators';
import {ProgressbarService} from '@app/services';

@Injectable()
export class ProgressInterceptor implements HttpInterceptor {

  requestCount = 0;

  constructor(private progressbarService: ProgressbarService) {
  }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (this.requestCount === 0) {
      this.progressbarService.show();
    }
    this.requestCount++;
    return next
      .handle(request)
      .pipe(
        // @ts-ignore
        filter(event => event instanceof HttpResponse),
        tap((event: HttpResponse<any>) => {
          this.requestCount--;
          if (this.requestCount === 0) {
            this.progressbarService.hide();
          }
          return event;
        }),
        catchError((error: any, caught: Observable<any>) => {
          return throwError(this.errorHandler(error, caught));
        })
      );
  }

  errorHandler(error: any, caught: Observable<any>): Observable<any> {
    if (error) {
      this.progressbarService.hide();
    }
    return error;
  }
}
