import { Component, OnInit } from '@angular/core';
import { Chapter, Book } from '@app/models';
import {
  HadithService,
  HelperService,
  ProgressbarService,
} from '@app/services';
import { ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialog } from '@angular/material/dialog';
import { SearchCompComponent } from '@app/components/component/search-comp/search-comp.component';
import { NoopScrollStrategy } from '@angular/cdk/overlay';
import { IndexeddbService } from '@app/services/common/indexeddb.service';

@Component({
  selector: 'app-chapter-mobile',
  templateUrl: './chapter-mobile.component.html',
  styleUrls: ['./chapter-mobile.component.css'],
})
export class ChapterMobileComponent implements OnInit {
  title: string | undefined;
  chapters!: Chapter[];
  book!: Book;
  books!: Book[];
  bookId: number | undefined;
  footerMenus: any[] = [
    {
      id: 1,
      title: 'হোম',
      img: 'home.svg',
      slug: 'home',
      link: '/',
    },
  ];
  constructor(
    private hadithService: HadithService,
    private route: ActivatedRoute,
    public progressbarService: ProgressbarService,
    public helperService: HelperService,
    private snackBar: MatSnackBar,
    public dialog: MatDialog,
    private indexeddbService: IndexeddbService
  ) {}

  ngOnInit(): void {
    this.route.params.subscribe((params) => {
      if (params.id) {
        this.bookId = params.id;
        this.getChaptersByBook();
        this.getBooks();
      }
    });
  }

  getChaptersByBook(): void {
    if (this.bookId) {
      this.indexeddbService
        .getByIndex('books', 'id', parseInt(String(this.bookId), 0))
        .subscribe((item) => {
          this.book = item;
          this.footerMenus.push(
            {
              id: 4,
              title: 'খুঁজুন',
              img: 'search.svg',
              slug: 'search',
              link: '',
              book: this.book,
            },
            {
              id: 3,
              title: 'পিছনে',
              img: 'back.svg',
              slug: 'back',
              link: '/',
            }
          );
          if (this.book) {
            // tslint:disable-next-line:max-line-length
            this.indexeddbService
              .getAllByIndex(
                this.book.slug + 'Chapters',
                'book_id',
                parseInt(String(this.bookId), 0)
              )
              .subscribe((value) => {
                this.chapters = value.sort(
                  this.helperService.sortBy('sequence')
                );
              });
          }
        });
    }
  }

  getBooks(): void {
    this.indexeddbService.getAll('books').subscribe((item) => {
      this.books = item;
    });
  }

  openSnackBar(): void {
    this.snackBar.open('এই অধ্যায়ের হাদীসের কাজ চলমান', '', {
      duration: 2000,
      verticalPosition: 'top',
    });
  }
}
