  fuseHadithSearch(
    values: any[],
    q: string,
    searchOptions: SearchOptions
  ): Observable<any[]> {
    const fuse = new Fuse(values, searchOptions);
    return of(
      this.highlight(
        fuse.search(q).filter((item) => {
          // console.log(item);
          if (item.score) {
            return item.score < 0.3;
          } else {
            return false;
          }
        })
      )
    );
    // return of(this.highlight(fuse.search(q)));
  }

