import { NgModule } from '@angular/core';
import { ExtraOptions, RouterModule, Routes } from '@angular/router';

import { HadithMobileComponent } from '@app/components/pages/hadith-mobile/hadith-mobile.component';
import { BookMobileComponent } from '@app/components/pages/book-mobile/book-mobile.component';
import { ChapterMobileComponent } from '@app/components/pages/chapter-mobile/chapter-mobile.component';
import { BookmarkHadithComponent } from '@app/components/pages/bookmark-hadith/bookmark-hadith.component';
import { LastReadHadithComponent } from '@app/components/pages/last-read-hadith/last-read-hadith.component';
import { HadithBookDescComponent } from './components/pages/hadith-book-desc/hadith-book-desc.component';
import { TitleMobileComponent } from './components/pages/title-mobile/title-mobile.component';
import { SettingsComponent } from '@app/components/pages/settings/settings.component';
import { SingleHadithMobileComponent } from '@app/components/pages/single-hadith-mobile/single-hadith-mobile.component';

const routes: Routes = [
  {
    path: '',
    component: BookMobileComponent,
    data: {
      title: 'হাদীস',
    },
  },
  {
    path: 'mobile-book/:book_id/chapter/:chapter_id/hadiths',
    component: HadithMobileComponent,
    data: {
      title: 'হাদীস',
    },
  },
  {
    path: 'mobile-book/:book_id/chapter/:chapter_id/titles',
    component: TitleMobileComponent,
    data: {
      title: 'পরিচ্ছেদ',
    },
  },
  {
    path: 'mobile-book/:book_id/chapter/:chapter_id/title/:title_id/hadiths',
    component: HadithMobileComponent,
    data: {
      title: 'হাদীস',
    },
  },
  {
    path: 'mobile-book/hadith/:id',
    component: SingleHadithMobileComponent,
    data: {
      title: 'হাদীস',
    },
  },
  {
    path: 'mobile-hadith-bookmarks',
    component: BookmarkHadithComponent,
    data: {
      title: 'সংরক্ষিত হাদীস',
    },
  },
  {
    path: 'last-read-hadith',
    component: LastReadHadithComponent,
    data: {
      title: 'সর্বশেষ পঠিত হাদীস',
    },
  },
  {
    path: 'hadith-mobile/search',
    component: HadithMobileComponent,
    data: {
      title: 'হাদীস সার্চ',
    },
  },
  {
    path: 'mobile-book/:id/chapters',
    component: ChapterMobileComponent,
    data: {
      title: 'অধ্যায়',
    },
  },
  {
    path: 'book-description/:id',
    component: HadithBookDescComponent,
    data: {
      title: 'হাদীসের কিতাব',
    },
  },
  {
    path: 'settings',
    component: SettingsComponent,
    data: {
      title: 'সেটিংস',
    },
  },
  // otherwise redirect to homepage
  { path: '**', redirectTo: '' },
];

const routerOptions: ExtraOptions = {
  scrollPositionRestoration: 'enabled',
  // onSameUrlNavigation: 'reload'
  // anchorScrolling: 'enabled',
  // scrollOffset: [0, 100] // [x, y]
  // ...any other options you'd like to use
};

@NgModule({
  imports: [RouterModule.forRoot(routes, routerOptions)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
