import {Component, Inject, OnInit} from '@angular/core';
import {
    MatDialog,
    MAT_DIALOG_DATA,
    MatDialogRef,
} from '@angular/material/dialog';
import {HadithService} from '@app/services';
import {Book} from '@app/models';
import {IndexeddbService} from '@app/services/common/indexeddb.service';

@Component({
  selector: 'app-download-hadith-modal',
  templateUrl: './download-hadith-modal.component.html',
  styleUrls: ['./download-hadith-modal.component.css']
})
export class DownloadHadithModalComponent implements OnInit{
    chaptersDownloading = false;
    titlesDownloading = false;
    hadithsDownloading = false;
    constructor(
        public dialog: MatDialog,
        public dialogRef: MatDialogRef<DownloadHadithModalComponent>,
        private hadithService: HadithService,
        private indexeddbService: IndexeddbService,
        @Inject(MAT_DIALOG_DATA) public data: { book: Book, type: string}
    ) {}

    async ngOnInit() {
        if (this.data?.book) {
          switch (this.data?.type) {
            case 'chapters':
              await this.downloadChapters(this.data.book.slug);
              break;
            case 'titles':
              await this.downloadTitles(this.data.book.slug);
              break;
            case 'hadiths':
              await this.downloadHadiths(this.data.book.slug);
              break;
            default:
              await this.downloadChapters(this.data.book.slug);
              await this.downloadTitles(this.data.book.slug);
              await this.downloadHadiths(this.data.book.slug);
          }
        }
    }

    downloadChapters(slug: string) {
        this.chaptersDownloading = true;
        this.hadithService.downloadChaptersBySlug(slug).subscribe((res) => {
            const chapters = res.data.rows;
            this.indexeddbService.bulkAdd(slug + 'Chapters', chapters);
            if (this.data?.type === 'chapters') {
              this.hadithsDownloading = false;
              this.indexeddbService.setDownloadedValue({book: slug, downloaded: true});
              this.dialogRef.close();
            }
          });
    }
    downloadTitles(slug: string) {
        this.titlesDownloading = true;
        this.hadithService.downloadTitlesBySlug(slug).subscribe((res) => {
            const titles = res.data.rows;
            this.indexeddbService.bulkAdd(slug + 'Titles', titles);
            if (this.data?.type === 'titles') {
              this.hadithsDownloading = false;
              this.indexeddbService.setDownloadedValue({book: slug, downloaded: true});
              this.dialogRef.close();
            }
            this.titlesDownloading = false;

        });
    }
    downloadHadiths(slug: string) {
        this.hadithsDownloading = true;
        this.hadithService.downloadHadithsBySlug(slug).subscribe((res) => {
            const hadiths = res.data.rows;            
            this.indexeddbService.bulkAdd(slug + 'Hadiths', hadiths).subscribe(value => {
                this.hadithsDownloading = false;
                this.indexeddbService.setDownloadedValue({book: slug, downloaded: true});
                this.dialogRef.close();
            });
        });
    }

  isVisible(type: string | undefined, givenType: string) {
    switch (type) {
      case 'chapters':
        return givenType === type;
      case 'titles':
        return givenType === type;
      case 'hadiths':
        return givenType === type;
      case 'all':
        return true;
      default:
        return 'none';
    }
  }
}
