import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
    providedIn: 'root',
})
export class ThemeService {
    private currentThemeSubject = new BehaviorSubject<string>('light');
    private currentArabicTextSubject = new BehaviorSubject<string>('true');
    constructor() {
        const getThemeFromLocalStorage = localStorage.getItem(
            'theme'
        ) as string;
        if (getThemeFromLocalStorage) {
            this.currentThemeSubject.next(getThemeFromLocalStorage);
        }
        const getArabicTextFromLocalStorage = localStorage.getItem(
            'arText'
        ) as string;
        if (getArabicTextFromLocalStorage) {
            this.currentArabicTextSubject.next(getArabicTextFromLocalStorage);
        }
    }
    public setCurrentTheme(theme: string) {
        this.currentThemeSubject.next(theme);
        localStorage.setItem('theme', theme);
    }

    public get getCurrentTheme(): string {
        return this.currentThemeSubject.value;
    }
    public setCurrentArabicText(arText: string) {
        this.currentArabicTextSubject.next(arText);
        localStorage.setItem('arText', arText);
    }

    public get getCurrentArabicText(): string {
        return this.currentArabicTextSubject.value;
    }
}
