import { Component, OnInit } from '@angular/core';
import { Book } from '@app/models';
import { HelperService } from '@app/services';
import { ActivatedRoute } from '@angular/router';
import { IndexeddbService } from '@app/services/common/indexeddb.service';

@Component({
  selector: 'app-hadith-book-desc',
  templateUrl: './hadith-book-desc.component.html',
  styleUrls: ['./hadith-book-desc.component.css'],
})
export class HadithBookDescComponent implements OnInit {
  book!: Book;
  books!: Book[];
  footerMenus: any[] = [
    {
      id: 1,
      title: 'হোম',
      img: 'home.svg',
      slug: 'home',
      link: '/',
    },
  ];
  constructor(
    public helperService: HelperService,
    private activatedRoute: ActivatedRoute,
    private indexeddbService: IndexeddbService
  ) {}

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((param) => {
      if (param.id) {
        this.getBook(param.id);
      }
      this.getBooks();
    });
  }

  getBook(bookId: any): void {
    this.indexeddbService
      .getByIndex('books', 'id', parseInt(String(bookId), 0))
      .subscribe((item) => {
        this.book = item;
        this.footerMenus.push(
          {
            id: 4,
            title: 'খুঁজুন',
            img: 'search.svg',
            slug: 'search',
            link: '',
            book: this.book,
          },
          {
            id: 3,
            title: 'পিছনে',
            img: 'back.svg',
            slug: 'back',
            link: '/',
          }
        );
      });
  }

  getBooks(): void {
    this.indexeddbService.getAll('books').subscribe((item) => {
      this.books = item
        .sort(this.helperService.sortBy('sequence'))
        .filter((value: Book) => {
          return value.downloaded;
        });
    });
  }
}
