import {
  Component,
  OnInit,
  ViewEncapsulation,
} from '@angular/core';
import { Chapter, Book } from '@app/models';
import { HadithService, HelperService } from '@app/services';
import { ActivatedRoute, Router } from '@angular/router';
import { FormGroup, FormBuilder } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { IndexeddbService } from '@app/services/common/indexeddb.service';

@Component({
  selector: 'app-title-mobile',
  templateUrl: './title-mobile.component.html',
  styleUrls: ['./title-mobile.component.css'],
  encapsulation: ViewEncapsulation.None,
})
export class TitleMobileComponent implements OnInit {
  titles: any;
  chapter!: Chapter;
  book!: Book;
  books!: Book[];
  bookId: number | undefined;
  chapterId!: number;
  chapters!: Chapter[];
  filterForm!: FormGroup;
  page = 1;
  footerMenus: any[] = [
    {
      id: 1,
      title: 'হোম',
      img: 'home.svg',
      slug: 'home',
      link: '/',
    },
  ];

  constructor(
    private hadithService: HadithService,
    private route: ActivatedRoute,
    private router: Router,
    public dialog: MatDialog,
    public helperService: HelperService,
    private formBuilder: FormBuilder,
    private indexeddbService: IndexeddbService
  ) {
    this.createForm();
  }

  createForm() {
    this.filterForm = this.formBuilder.group({
      book: [''],
      chapter: [''],
    });
  }

  ngOnInit(): void {
    this.route.params.subscribe((params) => {
      this.bookId = params.book_id;
      this.chapterId = params.chapter_id;
      this.getBookAndChapters();
      this.getBooks();
    });
  }

  getTitlesByBookAndChapter(book: Book, chapterId: number): void {
    this.indexeddbService
      .getAllByIndex(
        book.slug + 'Titles',
        'chapter_id',
        parseInt(String(chapterId), 0)
      )
      .subscribe((item) => {
        this.titles = item.sort(this.helperService.sortBy('sequence'));
      });
  }

  getBookAndChapters(): void {
    if (this.bookId) {
      this.indexeddbService
        .getByIndex('books', 'id', parseInt(String(this.bookId), 0))
        .subscribe((item) => {
          this.book = item;
          if (this.footerMenus.length > 1) {
            this.footerMenus.map((item2) => {
              if (item2.slug === 'search') {
                return {
                  ...item2,
                  book: this.book,
                };
              } else if (item2.slug === 'back') {
                return {
                  ...item2,
                  link: '/mobile-book/' + this.book.id + '/chapters',
                };
              } else {
                return item2;
              }
            });
          } else {
            this.footerMenus.push(
              {
                id: 4,
                title: 'খুঁজুন',
                img: 'search.svg',
                slug: 'search',
                link: '',
                book: this.book,
              },
              {
                id: 3,
                title: 'পিছনে',
                img: 'back.svg',
                slug: 'back',
                link: '/mobile-book/' + this.book.id + '/chapters',
              }
            );
          }

          this.filterForm.controls.book.patchValue(this.book.id);
          if (this.book) {
            // tslint:disable-next-line:max-line-length
            this.indexeddbService
              .getAllByIndex(
                this.book.slug + 'Chapters',
                'book_id',
                parseInt(String(this.bookId), 0)
              )
              .subscribe((value) => {
                this.chapters = value.sort(
                  this.helperService.sortBy('sequence')
                );
                this.chapterId = this.chapterId || this.chapters[0].id;
                this.getChapter(this.book, this.chapterId);
              });
          }
        });
    }
  }

  getChapter(book: Book, chapterId: number) {
    this.indexeddbService
      .getByIndex(book.slug + 'Chapters', 'id', parseInt(String(chapterId), 0))
      .subscribe((item) => {
        this.chapter = item;
        this.filterForm.controls.chapter.patchValue(this.chapter.id);
        this.getTitlesByBookAndChapter(book, chapterId);
      });
  }

  getBooks(): void {
    this.indexeddbService.getAll('books').subscribe((item) => {
      this.books = item
        .sort(this.helperService.sortBy('sequence'))
        .filter((value: Book) => {
          return value.downloaded;
        });
    });
  }

  bookChange() {
    this.hadithService
      .getChaptersByBookId(this.filterForm.value.book)
      .subscribe((res) => {
        const chapterId = res.data.rows[0]?.id;
        this.router.navigate(
          [
            `/mobile-book/${this.filterForm.value.book}/chapter/${chapterId}/titles`,
          ],
          {
            queryParams: { page: 1 },
            queryParamsHandling: 'merge',
          }
        );
      });
  }

  chapterChange() {
    this.router.navigate(
      [
        `/mobile-book/${this.filterForm.value.book}/chapter/${this.filterForm.value.chapter}/titles`,
      ],
      {
        queryParams: { page: 1 },
        queryParamsHandling: 'merge',
      }
    );
  }

  shorten(text: string): string {
    const returnText = text.slice(0, 30);
    if (returnText.length === 30) {
      return returnText + '...';
    } else {
      return returnText;
    }
  }
}
