import { Injectable, Optional } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Sort } from '@angular/material/sort';
import { MatDialog } from '@angular/material/dialog';
import { NoopScrollStrategy } from '@angular/cdk/overlay';
import { SnackBarService } from './snackbar.service';

@Injectable({
    providedIn: 'root',
})
export class HelperService {
    constructor(
        @Optional() public dialog: MatDialog,
        private snackBarService: SnackBarService
    ) {}

    static buildQueryParams(options: any) {
        return Object.entries(options)
            .map(([key, val]) => {
                if (val) {
                    return `${key}=${val}`;
                }
            })
            .filter(Boolean)
            .join('&');
    }

    static getSearchParams(
        form: FormGroup | undefined,
        options: any,
        sort: Sort | undefined
    ) {
        const page = options.page ? options.page : 1;
        // @ts-ignore
        form.get('page').patchValue(page, { emitEvent: false });

        if (options.order) {
            // @ts-ignore
            form.get('order').patchValue(options.order, { emitEvent: false });
        }
        if (options.direction) {
            // @ts-ignore
            form.get('direction').patchValue(options.direction, {
                emitEvent: false,
            });
        }

        if (sort) {
            const order = sort.direction ? sort.active : '';
            const direction = sort.direction ? sort.direction : '';
            // @ts-ignore
            form.get('order').patchValue(order, { emitEvent: false });
            // @ts-ignore
            form.get('direction').patchValue(direction, { emitEvent: false });
        }

        // @ts-ignore
        return { ...options, ...form.value };
    }

    static shortString(str: string, length: number = 10): string {
        const cleanStr = this.newLineToSpace(this.removeHtmlTag(str));
        const trimmedStr = cleanStr.substr(0, length);
        if (cleanStr.length > trimmedStr.length) {
            return trimmedStr.substr(
                0,
                Math.min(trimmedStr.length, trimmedStr.lastIndexOf(' '))
            );
        }
        return trimmedStr;
    }

    static removeHtmlTag(str: string): string {
        const htmlRemoved = str.trim().replace(/<\/?[^>]+(>|$)/g, '');
        return htmlRemoved.replace(/\&nbsp;/g, '');
    }

    static newLineToBreak(str: string): string {
        return str.replace(/\n/g, '<br />');
    }

    static newLineToSpace(str: string): string {
        return str.replace(/\n/g, ' ');
    }

    static isEqual(obj1: any, obj2: any): boolean {
        return JSON.stringify(obj1) === JSON.stringify(obj2);
    }

    changeBengaliToEnglish(str: string): string {
        const finalEnlishToBanglaNumber = {
            '০': 0,
            '১': 1,
            '২': 2,
            '৩': 3,
            '৪': 4,
            '৫': 5,
            '৬': 6,
            '৭': 7,
            '৮': 8,
            '৯': 9,
        };
        let retStr = str;
        // tslint:disable-next-line:forin
        for (const x in finalEnlishToBanglaNumber) {
            retStr = retStr.replace(
                new RegExp(x, 'g'),
                finalEnlishToBanglaNumber[x]
            );
        }
        return retStr;
    }

    changeEnglishToBengali(str: string | undefined | null): string {
        if (!str) {
          return '';
        }
        const finalEnlishToBanglaNumber = {
            0: '০',
            1: '১',
            2: '২',
            3: '৩',
            4: '৪',
            5: '৫',
            6: '৬',
            7: '৭',
            8: '৮',
            9: '৯',
        };
        let retStr = str;
        // tslint:disable-next-line:forin
        for (const x in finalEnlishToBanglaNumber) {
            // @ts-ignore
            retStr = retStr.replace(
                new RegExp(x, 'g'),
                finalEnlishToBanglaNumber[x]
            );
        }
        return retStr;
    }

    changeEnglishToArabic(str: string): string {
        const finalEnlishToArabicNumber = {
            0: '۰',
            1: '١',
            2: '٢',
            3: '٣',
            4: '٤',
            5: '٥',
            6: '٦',
            7: '٧',
            8: '٨',
            9: '٩',
        };
        let retStr = str;
        // tslint:disable-next-line:forin
        for (const x in finalEnlishToArabicNumber) {
            // @ts-ignore
            retStr = retStr.replace(
                new RegExp(x, 'g'),
                finalEnlishToArabicNumber[x]
            );
        }
        return retStr;
    }

    newLineAdder(str): string {
        if (str) {
            return str.replace(/\n/g, '<br />');
        }
        return '';
    }

    addAnchorTag(str) {
        let match = str.match(
            /(\b(https?):\/\/[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/gi
        );
        let final = str;
        if (match) {
            match.map((url) => {
                final = final.replace(
                    url,
                    '<a href="' + url + '" target="_BLANK">' + url + '</a>'
                );
            });
        }
        return final;
    }

    newLine(str: string): string {
        return str.replace(/<br>/g, '\n');
    }

  // tslint:disable-next-line:typedef
    addHtmlTag(str): string {
      if (str) {
        const htmlRemoved = str.replace(/&lt;p&gt;/g, '<p>');
        return htmlRemoved
          .replace(/&lt;\/p&gt;/g, '</p>')
          .replace(/&amp;#39;/g, '’')
          .replace(/&amp;rsquo;/g, '’')
          .replace(/&amp;lsquo;/g, '‘')
          .replace(/&amp;mdash;/g, '-')
          .replace(/&amp;zwnj;/g, '&zwnj;')
          .replace(/&amp;quot;/g, '″')
          .replace(/&lt;hr \/&gt;/g, '<hr/>')
          .replace(/&lt;br \/&gt;/g, '<br />')
          .replace(/&lt;strong&gt;/g, '<strong>')
          .replace(/&lt;\/strong&gt;/g, '</strong>')
          .replace(/&lt;p dir=&quot;rtl&quot;&gt;/g, '<p>')
          .replace(/&#039;/g, '’')
          .replace(/&rsquo;/g, '’')
          .replace(/&rlm;/g, '')
          .replace(/&quot;/g, '"')
          .replace(/&amp;ldquo;/g, '“')
          .replace(/&amp;rdquo;/g, '”')
          .replace(/&ndash;/g, '–')
          .replace(/\(ইফাঃ\)/g, '')
          .replace(/-/g, '&#8211')
          .replace(/&zwnj;/g, '');
      } else {
        return '';
      }

    }

    sortBy(field: any) {
        return (a: any, b: any) => {
            // @ts-ignore
            return (a[field] > b[field]) - (a[field] < b[field]);
        };
    }
}
