import { Component, OnInit } from '@angular/core';
import { Hadith, Book, Chapter } from '@app/models';
import { HadithService, HelperService, ThemeService } from '@app/services';
import { ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { IndexeddbService } from '@app/services/common/indexeddb.service';
import { forkJoin, Observable } from 'rxjs';

@Component({
  selector: 'app-hadith-mobile',
  templateUrl: './hadith-mobile.component.html',
  styleUrls: ['./hadith-mobile.component.css'],
})
export class HadithMobileComponent implements OnInit {
  footerMenus: any[] = [
    {
      id: 1,
      title: 'হোম',
      img: 'home.svg',
      slug: 'home',
      link: '/',
    },
  ];
  arabicText = this.themeService.getCurrentArabicText;
  hadiths!: Hadith[];
  page = 1;
  total = 0;
  title: any;
  titles: any;
  chapter!: Chapter;
  chapters!: Chapter[];
  book!: Book;
  books!: Book[];
  bookId: number | undefined;
  chapterId: number | undefined;
  titleId: number | undefined;
  searchForm!: FormGroup;
  filterForm!: FormGroup;
  isSearching = false;
  loading = true;
  loadingForSearch = false;

  constructor(
    private hadithService: HadithService,
    private route: ActivatedRoute,
    private formBuilder: FormBuilder,
    public dialog: MatDialog,
    public helperService: HelperService,
    private indexeddbService: IndexeddbService,
    private themeService: ThemeService,
    private router: Router
  ) {
    this.createForm();
  }

  createForm() {
    this.filterForm = this.formBuilder.group({
      book: [''],
      chapter: [''],
      title: [''],
    });
    this.searchForm = this.formBuilder.group({
      q: [''],
      book: [''],
      hadith_number: [''],
    });
  }

  ngOnInit(): void {
    this.route.params.subscribe((params) => {
      if (params.book_id && params.chapter_id && !params.title_id) {
        this.isSearching = false;
        this.bookId = params.book_id;
        this.chapterId = params.chapter_id;
        this.getBookAndChapters();
        this.getBooks();
      } else if (params.book_id && params.chapter_id && params.title_id) {
        this.isSearching = false;
        this.bookId = params.book_id;
        this.chapterId = params.chapter_id;
        this.titleId = params.title_id;
        this.getBookAndChapters();
        this.getBooks();
      }
    });
    this.route.queryParams.subscribe((queryParams) => {
      if (queryParams.q || queryParams.book || queryParams.hadith_no) {
        this.isSearching = true;
        this.bookId = parseInt(queryParams.book, 0);
        this.page = queryParams.page ? queryParams.page : 1;
        this.searchForm = this.formBuilder.group({
          q: [queryParams.q],
          book: [queryParams.book],
          hadith_number: [queryParams.hadith_no],
        });
        this.getSearchHadiths();
        this.getBooks();
        if (this.footerMenus.length > 1) {
          this.footerMenus[1] = {
            id: 4,
            title: 'খুঁজুন',
            img: 'search.svg',
            slug: 'search',
            link: '',
            book: this.book,
          };
          this.footerMenus[2] = {
            id: 3,
            title: 'পিছনে',
            img: 'back.svg',
            slug: 'back',
            link: '/',
          };
        } else {
          this.footerMenus.push(
            {
              id: 4,
              title: 'খুঁজুন',
              img: 'search.svg',
              slug: 'search',
              link: '',
              book: this.book,
            },
            {
              id: 3,
              title: 'পিছনে',
              img: 'back.svg',
              slug: 'back',
              link: '/',
            }
          );
        }
      }
    });
  }

  getBookAndChapters(): void {
    this.loading = true;
    if (this.bookId) {
      this.indexeddbService
        .getByIndex('books', 'id', parseInt(String(this.bookId), 0))
        .subscribe((item) => {
          this.book = item;
          this.filterForm.controls.book.patchValue(this.book.id);
          if (this.book) {
            // tslint:disable-next-line:max-line-length
            this.indexeddbService
              .getAllByIndex(
                this.book.slug + 'Chapters',
                'book_id',
                parseInt(String(this.bookId), 0)
              )
              .subscribe((value) => {
                this.chapters = value.sort(
                  this.helperService.sortBy('sequence')
                );
                this.chapterId = this.chapterId || this.chapters[0].id;
                this.loading = false;
                this.getChapter(this.book, this.chapterId);
              });
          }
        });
    }
  }

  getBooks(): void {
    this.loading = true;
    this.indexeddbService.getAll('books').subscribe((item) => {
      this.books = item
        .sort(this.helperService.sortBy('sequence'))
        .filter((value: Book) => {
          return value.downloaded;
        });
      this.loading = false;
    });
  }

  getChapter(book: Book, chapterId: number) {
    this.loading = true;
    this.indexeddbService
      .getByIndex(book.slug + 'Chapters', 'id', parseInt(String(chapterId), 0))
      .subscribe((item) => {
        this.chapter = item;
        this.filterForm.controls.chapter.patchValue(this.chapter.id);
        this.loading = false;
        if (this.titleId) {
          if (this.footerMenus.length > 2) {
            this.footerMenus[1] = {
              id: 4,
              title: 'খুঁজুন',
              img: 'search.svg',
              slug: 'search',
              link: '',
              book: this.book,
            };
            this.footerMenus[2] = {
              id: 3,
              title: 'পিছনে',
              img: 'back.svg',
              slug: 'back',
              link:
                '/mobile-book/' +
                this.book.id +
                '/chapter/' +
                this.chapter.id +
                '/titles',
            };
          } else {
            this.footerMenus.push(
              {
                id: 4,
                title: 'খুঁজুন',
                img: 'search.svg',
                slug: 'search',
                link: '',
                book: this.book,
              },
              {
                id: 3,
                title: 'পিছনে',
                img: 'back.svg',
                slug: 'back',
                link:
                  '/mobile-book/' +
                  this.book.id +
                  '/chapter/' +
                  this.chapter.id +
                  '/titles',
              }
            );
          }
          this.getTitlesByBookAndChapter(book, chapterId);
        } else {
          if (this.footerMenus.length > 2) {
            this.footerMenus[1] = {
              id: 4,
              title: 'খুঁজুন',
              img: 'search.svg',
              slug: 'search',
              link: '',
              book: this.book,
            };
            this.footerMenus[2] = {
              id: 3,
              title: 'পিছনে',
              img: 'back.svg',
              slug: 'back',
              link: '/mobile-book/' + this.book.id + '/chapters',
            };
          } else {
            this.footerMenus.push(
              {
                id: 4,
                title: 'খুঁজুন',
                img: 'search.svg',
                slug: 'search',
                link: '',
                book: this.book,
              },
              {
                id: 3,
                title: 'পিছনে',
                img: 'back.svg',
                slug: 'back',
                link: '/mobile-book/' + this.book.id + '/chapters',
              }
            );
          }
          this.getHadithsByBookAndChapterId(book, chapterId);
        }
      });
  }

  getTitleByBookAndId(book: Book, titleId: number) {
    this.loading = true;
    this.indexeddbService
      .getByIndex(book.slug + 'Titles', 'id', parseInt(String(titleId), 0))
      .subscribe((item) => {
        this.title = item;
        this.loading = false;
        this.filterForm.controls.title.patchValue(this.title.id);
      });
  }

  getTitlesByBookAndChapter(book: Book, chapterId: number): void {
    this.loading = true;
    this.indexeddbService
      .getAllByIndex(
        book.slug + 'Titles',
        'chapter_id',
        parseInt(String(chapterId), 0)
      )
      .subscribe((item) => {
        this.titles = item.sort(this.helperService.sortBy('sequence'));
        this.titleId = this.titleId || this.titles[0].id;
        this.loading = false;
        // @ts-ignore
        this.getTitleByBookAndId(book, this.titleId);
        // @ts-ignore
        this.getHadithsByBookAndTitleId(book, this.titleId);
      });
  }

  getHadithsByBookAndTitleId(book: Book, titleId: number): void {
    this.loading = true;
    this.indexeddbService
      .getAllByIndex(
        book.slug + 'Hadiths',
        'title_id',
        parseInt(String(titleId), 0)
      )
      .subscribe((item) => {
        this.hadiths = item.sort(this.helperService.sortBy('hadith_number'));
        this.total = this.hadiths.length;
        this.loading = false;
      });
  }

  getHadithsByBookAndChapterId(book: Book, chapterId: number): void {
    this.loading = true;
    this.indexeddbService
      .getAllByIndex(
        book.slug + 'Hadiths',
        'chapter_id',
        parseInt(String(chapterId), 0)
      )
      .subscribe((item) => {
        this.hadiths = item.sort(this.helperService.sortBy('hadith_number'));
        this.total = this.hadiths.length;
        this.loading = false;
      });
  }
  bookChange(event) {
    if (event.has_title) {
      this.hadithService
        .getChaptersByBookId(this.filterForm.value.book)
        .subscribe((res) => {
          const chapterId = res.data.rows[0]?.id;
          this.hadithService
            .getAllTitlesByBookAndChapter(this.filterForm.value.book, chapterId)
            .subscribe(async (res1) => {
              const titleId = res1.data.rows[0]?.id;
              await this.router.navigate(
                [
                  `/mobile-book/${this.filterForm.value.book}/chapter/${chapterId}/title/${titleId}/hadiths`,
                ],
                {
                  queryParams: { page: 1 },
                  queryParamsHandling: 'merge',
                }
              );
            });
        });
    } else {
      this.hadithService
        .getChaptersByBookId(this.filterForm.value.book)
        .subscribe(async (res) => {
          const chapterId = res.data.rows[0]?.id;
          await this.router.navigate(
            [
              `/mobile-book/${this.filterForm.value.book}/chapter/${chapterId}/hadiths`,
            ],
            {
              queryParams: { page: 1 },
              queryParamsHandling: 'merge',
            }
          );
        });
    }
  }

  async chapterChange() {
    if (this.titleId) {
      this.hadithService
        .getAllTitlesByBookAndChapter(
          this.filterForm.value.book,
          this.filterForm.value.chapter
        )
        .subscribe(async (res) => {
          const titleId = res.data.rows[0]?.id;
          await this.router.navigate(
            [
              `/mobile-book/${this.filterForm.value.book}/chapter/${this.filterForm.value.chapter}/title/${titleId}/hadiths`,
            ],
            {
              queryParams: { page: 1 },
              queryParamsHandling: 'merge',
            }
          );
        });
    } else {
      await this.router.navigate(
        [
          `/mobile-book/${this.filterForm.value.book}/chapter/${this.filterForm.value.chapter}/hadiths`,
        ],
        {
          queryParams: { page: 1 },
          queryParamsHandling: 'merge',
        }
      );
    }
  }

  async titleChange() {
    await this.router.navigate(
      [
        // tslint:disable-next-line:max-line-length
        `/mobile-book/${this.filterForm.value.book}/chapter/${this.filterForm.value.chapter}/title/${this.filterForm.value.title}/hadiths`,
      ],
      {
        queryParams: { page: 1 },
        queryParamsHandling: 'merge',
      }
    );
  }

  getSearchHadiths(): void {
    this.loadingForSearch = true;
    const q = this.searchForm.value.q;
    this.bookId = this.searchForm.value.book;
    const hadithNo = this.helperService.changeBengaliToEnglish(
      this.searchForm.value.hadith_number.trim()
    );
    if (hadithNo) {
      if (this.bookId) {
        this.indexeddbService
          .getByIndex('books', 'id', parseInt(String(this.bookId), 0))
          .subscribe((item) => {
            this.book = item;
            if (this.book) {
              // tslint:disable-next-line:max-line-length
              this.indexeddbService
                .getByIndex(
                  this.book.slug + 'Hadiths',
                  'hadith_number',
                  parseInt(String(hadithNo), 0)
                )
                .subscribe((value) => {
                  this.hadiths = [];
                  this.loadingForSearch = false;
                  if (value) {
                    this.hadiths.push(value);
                    this.total = this.hadiths.length;
                  }
                });
            }
          });
      }
    } else {
      const options = {
        keys: ['description', 'description_ar', 'title', 'title_ar'], // Specify the keys in your data objects to search on
        shouldSort: true,
        matchAllTokens: false,
        findAllMatches: true,
        includeScore: true,
        ignoreFieldNorm: true,
        threshold: 0.3,
        ignoreLocation: true,
        maxPatternLength: 32,
        minMatchCharLength: 1,
        includeMatches: true,
      };
      if (this.bookId) {
        this.indexeddbService
          .getByIndex('books', 'id', parseInt(String(this.bookId), 0))
          .subscribe((item) => {
            this.book = item;
            if (this.book) {
              this.indexeddbService
                .getAll(this.book.slug + 'Hadiths')
                .subscribe((res) => {
                  this.hadiths = [];
                  this.indexeddbService
                    .fuseHadithSearch(res, q, options)
                    .subscribe((values) => {
                      this.hadiths = values;
                      // this.hadiths = values.slice(0, 500);
                      this.loadingForSearch = false;
                      this.total = this.hadiths.length;
                    });
                });
            }
          });
      } else {
        this.indexeddbService.getAll('books').subscribe((books) => {
          const source: Observable<any>[] = [];
          this.hadiths = [];
          books.forEach((book) => {
            if (book.downloaded) {
              source.push(this.indexeddbService.getAll(book.slug + 'Hadiths'));
            }
          });
          forkJoin(source).subscribe((data) => {
            const total = [].concat.apply([], [...data]);
            this.indexeddbService
              .fuseHadithSearch(total, q, options)
              .subscribe((res) => {
                this.hadiths = res;
                // this.hadiths = res.slice(0, 500);
                this.loadingForSearch = false;
                this.total = this.hadiths.length;
              });
          });
        });
      }
    }
  }

  shorten(text: string): string {
    const returnText = text.slice(0, 30);
    if (returnText.length === 30) {
      return returnText + '...';
    } else {
      return returnText;
    }
  }
}
