import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Hadith, Book } from '@app/models';
import {
  HadithService,
  ProgressbarService,
  HelperService,
  ThemeService,
} from '@app/services';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-single-hadith-mobile',
  templateUrl: './single-hadith-mobile.component.html',
  styleUrls: ['./single-hadith-mobile.component.css'],
})
export class SingleHadithMobileComponent implements OnInit {
  hadith: any;
  books!: Book[];
  arabicText = this.themeService.getCurrentArabicText;
  tohabiBookId = 9;
  bukhariBookId = 1;
  muslimBookId = 2;
  fiqhUsSunanBookId = 14;
  misqatBookId = 13;
  footerMenus: any[] = [
    {
      id: 1,
      title: 'হোম',
      img: 'home.svg',
      slug: 'home',
      link: '/',
    },
    {
      id: 4,
      title: 'খুঁজুন',
      img: 'search.svg',
      slug: 'search',
      link: '',
    },
    {
      id: 3,
      title: 'পিছনে',
      img: 'back.svg',
      slug: 'goBack',
      link: '',
    },
  ];

  constructor(
    private route: ActivatedRoute,
    public helperService: HelperService,
    public progressbarService: ProgressbarService,
    private hadithService: HadithService,
    public dialog: MatDialog,
    private themeService: ThemeService
  ) {}

  ngOnInit(): void {
    this.route.params.subscribe((params) => {
      this.getHadith(params.id);
    });
  }

  getHadith(hadithId): void {
    this.hadithService.getHadith(hadithId).subscribe((res) => {
      this.hadith = res.data as Hadith;
      localStorage.setItem('hadith', JSON.stringify(this.hadith));
    });
  }

  shorten(text: string): string {
    const returnText = text.slice(0, 30);
    if (returnText.length === 30) {
      return returnText + '...';
    } else {
      return returnText;
    }
  }
}
