import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { HelperService, SnackBarService } from '@app/services';
import { HadithCopyDialogComponent } from '@app/components/component/hadith-copy-dialog/hadith-copy-dialog.component';
import { NoopScrollStrategy } from '@angular/cdk/overlay';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';

@Component({
  selector: 'app-mobile-hadith-list-comp',
  templateUrl: './mobile-hadith-list-comp.component.html',
  styleUrls: ['./mobile-hadith-list-comp.component.css'],
})
export class MobileHadithListCompComponent implements OnInit {
  @Input() hadiths: any = [];
  @Input() total = 0;
  @Input() page = 1;
  @Input() arabicText = 'true';
  @Input() isBookMark!: boolean;
  @Output() changePage = new EventEmitter();
  openedHadith: any;
  hadithContent = false;
  tohabiBookId = 9;
  bukhariBookId = 1;
  muslimBookId = 2;
  fiqhUsSunanBookId = 14;
  misqatBookId = 13;

  constructor(
    public helperService: HelperService,
    private snackBarService: SnackBarService,
    private dialog: MatDialog,
    private router: Router
  ) {}

  ngOnInit(): void {}

  showHadithContent(hadithId: any): void {
    this.openedHadith = hadithId;
    // tslint:disable-next-line:triple-equals
    if (hadithId == -1) {
      return;
    }
    this.hadithContent = true;
    this.setOpenedhadith(hadithId);
    const hadith = this.hadiths.find((h) => h.id === hadithId);
    localStorage.setItem('hadith', JSON.stringify(hadith));
  }

  setOpenedhadith(id: number): void {
    if (typeof document !== 'undefined') {
      const el: HTMLElement | null = document.getElementById(
        'hadith-' + id.toString()
      );
      if (el) {
        setTimeout(
          () =>
            el.scrollIntoView({
              behavior: 'smooth',
              block: 'start',
              inline: 'nearest',
            }),
          0
        );
      }
    }
  }

  hideHadithContent(): void {
    this.hadithContent = false;
  }

  bookmarkHadith(hadith: any, option: any) {
    // tslint:disable-next-line:triple-equals
    if (option == 'save') {
      const hadiths = localStorage.getItem('hadiths');
      if (hadiths?.length) {
        const hadithsArray = JSON.parse(hadiths);
        const isAlreadyAddedHadith = hadithsArray.find(
          (h) => h.id === hadith.id
        );
        if (!isAlreadyAddedHadith) {
          hadithsArray.push(hadith);
          localStorage.setItem('hadiths', JSON.stringify(hadithsArray));
          this.snackBarService.success(`হাদীসটি সংরক্ষণ করা হয়েছে।`);
        } else {
          const newHadiths = hadithsArray.filter((h) => h.id !== hadith.id);
          localStorage.setItem('hadiths', JSON.stringify(newHadiths));
          this.snackBarService.success(`হাদীসটি মুছে ফেলা হয়েছে।`);
        }
      } else {
        const hadithsArray: any = [];
        hadithsArray.push(hadith);
        localStorage.setItem('hadiths', JSON.stringify(hadithsArray));
        this.snackBarService.success(`হাদীসটি সংরক্ষণ করা হয়েছে।`);
      }
    }
    if (option === 'delete') {
      // tslint:disable-next-line:triple-equals
      this.hadiths = this.hadiths.filter((h) => h.id != hadith.id);
      localStorage.setItem('hadiths', JSON.stringify(this.hadiths));
      this.snackBarService.success(`হাদীসটি মুছে ফেলা হয়েছে।`);
    }
  }

  isSavedHadith(hadith: any) {
    const hadiths = localStorage.getItem('hadiths');
    if (hadiths?.length) {
      const hadithsArray = JSON.parse(hadiths);
      return hadithsArray.find((h) => h.id === hadith.id);
    }
  }

  openShareDialog(hadith: any): void {
    this.dialog.open(HadithCopyDialogComponent, {
      width: '350px',
      data: { hadith },
      autoFocus: false,
      scrollStrategy: new NoopScrollStrategy(),
    });
  }

  openPlayTTS(hadith: any): void {
    // @ts-ignore
    AndroidNative.playTTS(hadith.description_ar);
  }

  saveLastRead(hadith: any) {
    localStorage.setItem('hadith', JSON.stringify(hadith));
  }

  openNote(hadithId) {
    if (navigator.onLine) {
      this.router.navigate([`/mobile-book/hadith/${hadithId}`]);
    } else {
      this.snackBarService.warn(
        'হাদীসের শাস্ত্রীয় আলোচনা দেখতে আপনার নেট কানেকশন নিশ্চিত করুন'
      );
    }
  }

  openExplanation(hadithId) {
    if (navigator.onLine) {
      this.router.navigate([`/mobile-book/hadith/${hadithId}`]);
    } else {
      this.snackBarService.warn(
        'হাদীসের ব্যাখ্যা দেখতে আপনার নেট কানেকশন নিশ্চিত করুন'
      );
    }
  }
}
