import { Component, OnInit } from '@angular/core';
import { Book } from '@app/models';
import { HadithService, HelperService } from '@app/services';
import { MatDialog } from '@angular/material/dialog';
import { SearchCompComponent } from '@app/components/component/search-comp/search-comp.component';
import { NoopScrollStrategy } from '@angular/cdk/overlay';
import { IndexeddbService } from '@app/services/common/indexeddb.service';
import { Router } from '@angular/router';
import { DownloadHadithModalComponent } from '@app/components/component/download-hadith-modal/download-hadith-modal.component';

@Component({
  selector: 'app-book-mobile',
  templateUrl: './book-mobile.component.html',
  styleUrls: ['./book-mobile.component.css'],
})
export class BookMobileComponent implements OnInit {
  user: any;
  verificationStatus: any;
  books!: Book[];
  loading = false;
  modalLoading = false;

  footerMenus: any[] = [
    {
      id: 1,
      title: 'সেটিংস',
      img: 'settings.svg',
      slug: 'settings',
      link: '/settings',
    },
    {
      id: 2,
      title: 'সর্বশেষ পঠিত',
      img: 'ic_last_read.svg',
      slug: 'ic_last_read',
      link: '/last-read-hadith',
    },
    {
      id: 3,
      title: 'বুকমার্ক',
      img: 'ic_bookmark.svg',
      slug: 'ic_bookmark',
      link: '/mobile-hadith-bookmarks',
    },
    {
      id: 4,
      title: 'প্রস্থান',
      img: 'back.svg',
      slug: 'close',
      link: '',
    },
  ];
  constructor(
    private hadithService: HadithService,
    public dialog: MatDialog,
    private indexeddbService: IndexeddbService,
    public helperService: HelperService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.getBooks();
    this.modalLoading = false;
  }

  getBooks(): void {
    this.indexeddbService.count('books').subscribe((value) => {
      if (value === 0) {
        this.downloadBooks();
      } else {
        // tslint:disable-next-line:no-shadowed-variable
        this.indexeddbService.getAll('books').subscribe((item) => {
          this.books = item.sort(this.helperService.sortBy('sequence'));
        });
      }
    });
  }

  downloadBooks() {
    this.hadithService.getDownloadBooks().subscribe((res) => {
      this.books = res.data.rows.map((value) => {
        return { ...value, downloaded: false };
      });
      this.indexeddbService.bulkAdd('books', this.books);
    });
  }

  openSearchDialog(): void {
    this.dialog.open(SearchCompComponent, {
      data: {
        books: this.books
          .sort(this.helperService.sortBy('sequence'))
          .filter((value: Book) => {
            return value.downloaded;
          }),
      },
      width: '350px',
      autoFocus: false,
      scrollStrategy: new NoopScrollStrategy(),
    });
  }

  onBookClick(book: Book, type: string): void {
    if (!book?.downloaded) {
      console.log('book not downloaded');
      this.openDownloadDialog(book, type);
    } else {
      this.router.navigate([`/mobile-book/${book.id}/chapters`]);
    }
  }

  openDownloadDialog(book: Book, type: string): void {
    this.dialog.open(DownloadHadithModalComponent, {
      data: {
        book,
        type,
      },
      width: '350px',
      autoFocus: false,
      scrollStrategy: new NoopScrollStrategy(),
    });

    this.indexeddbService.bookDownloadedObservable.subscribe((value) => {
      if (value.book === book.slug) {
        this.books.forEach((bk, index) => {
          if (bk.slug === book.slug) {
            const updatedBook = JSON.parse(JSON.stringify(this.books[index]));
            updatedBook.downloaded = true;
            this.indexeddbService
              .update('books', updatedBook)
              .subscribe((i) => {
                this.getBooks();
              });
          }
        });
      }
    });
  }
}
