import { Component, OnInit, Inject } from '@angular/core';
import { HelperService, SnackBarService } from '@app/services';
import {
    MatDialogRef,
    MAT_DIALOG_DATA,
    MatDialog,
} from '@angular/material/dialog';
import { environment } from '@environments/environment';
export interface HadithShareDialogData {
    hadith: any;
}

@Component({
    selector: 'app-hadith-copy-dialog',
    templateUrl: './hadith-copy-dialog.component.html',
    styleUrls: ['./hadith-copy-dialog.component.css'],
})
export class HadithCopyDialogComponent implements OnInit {
    baseUrl = 'https://muslimbangla.com/';
    applink = true;
    website = true;
  // tslint:disable-next-line:variable-name
    title_ar = true;
  // tslint:disable-next-line:variable-name
    title_bn = true;
  // tslint:disable-next-line:variable-name
    hadith_ar = true;
  // tslint:disable-next-line:variable-name
    hadith_bn = true;
    type = true;
    rabi = true;
    tohabiBookId = 9;
    misqatBookId = 13;

    constructor(
        public helperService: HelperService,
        private snackBarService: SnackBarService,
        public dialog: MatDialog,
        public dialogRef: MatDialogRef<HadithCopyDialogComponent>,
        @Inject(MAT_DIALOG_DATA)
        public hadithShareDialogData: HadithShareDialogData
    ) {}

    ngOnInit(): void {
        const settingsObjectString = localStorage.getItem('hadithCopySettings');
        try {
            if (settingsObjectString) {
                // @ts-ignore
                const settingsObject = JSON.parse(settingsObjectString);
                this.applink =
                    settingsObject.applink !== undefined
                        ? settingsObject.applink
                        : true;
                this.website =
                    settingsObject.website !== undefined
                        ? settingsObject.website
                        : true;
                this.title_ar =
                    settingsObject.title_ar !== undefined
                        ? settingsObject.title_ar
                        : true;
                this.title_bn =
                    settingsObject.title_bn !== undefined
                        ? settingsObject.title_bn
                        : true;
                this.hadith_ar =
                    settingsObject.hadith_ar !== undefined
                        ? settingsObject.hadith_ar
                        : true;
                this.hadith_bn =
                    settingsObject.hadith_bn !== undefined
                        ? settingsObject.hadith_bn
                        : true;
                this.type =
                    settingsObject.type !== undefined
                        ? settingsObject.type
                        : true;
                this.rabi =
                    settingsObject.rabi !== undefined
                        ? settingsObject.rabi
                        : true;
            }
        } catch (e) {
            this.changeHadithShareLocalStorage();
        }
    }

    closeDialog(): void {
        this.dialogRef.close();
    }

    changeHadithShareLocalStorage(): void {
        const hadithCopySettings = {
            applink: this.applink,
            website: this.website,
            title_ar: this.title_ar,
            title_bn: this.title_bn,
            hadith_ar: this.hadith_ar,
            hadith_bn: this.hadith_bn,
            type: this.type,
            rabi: this.rabi,
        };
        localStorage.setItem(
            'hadithCopySettings',
            JSON.stringify(hadithCopySettings)
        );
    }

    newLine(str: string): string {
        return str.replace(/<br>/g, '\n');
    }

    onCopy(hadith: any): void {
      // tslint:disable-next-line:one-variable-per-declaration
        let textarea, result;
      // tslint:disable-next-line:variable-name
        let string = '';
        if (this.title_ar) {
            string += `${HelperService.removeHtmlTag(
                this.addHtmlTag(this.newLine(hadith.title_ar))
            )}\n\n`;
        }
        if (this.hadith_ar) {
            string += `${HelperService.removeHtmlTag(
                this.addHtmlTag(this.newLine(hadith.description_ar))
            )}\n\n`;
        }
        if (this.title_bn) {
            string += `পরিচ্ছেদঃ ${HelperService.removeHtmlTag(
                this.addHtmlTag(this.newLine(hadith.title))
            )}\n\n`;
        }
        if (this.hadith_bn) {
            string += `${HelperService.removeHtmlTag(
                this.addHtmlTag(this.newLine(hadith.description))
            )}\n\n`;
        }
        string += `${hadith.book.title}\n`;
      // tslint:disable-next-line:triple-equals
        if (hadith.book_id == this.tohabiBookId ||
          // tslint:disable-next-line:triple-equals
            hadith.book_id == this.misqatBookId) {
            string += `হাদীস নং: ${this.helperService.changeEnglishToBengali(
                hadith.hadith_number.toString()
            )}  `;
            if (hadith.hadith_number_one) {
                string += ` - ${this.helperService.changeEnglishToBengali(
                    hadith.hadith_number_one?.toString()
                )}`;
            }
        } else {
            string += `হাদীস নং: ${this.helperService.changeEnglishToBengali(
                hadith.hadith_number.toString()
            )}  `;
            if (hadith.hadith_number_one) {
                string += `আন্তর্জাতিক নং: ${this.helperService.changeEnglishToBengali(
                    hadith.hadith_number_one.toString()
                )}`;
            }
            if (hadith.hadith_number_two) {
                string += ` - ${this.helperService.changeEnglishToBengali(
                    hadith.hadith_number_two.toString()
                )}`;
            }
        }
        string += '\n\n';
        if (this.type) {
            if (hadith.book.id === 1 || hadith.book.id === 2) {
                string += `তাহকীকঃ তাহকীক নিষ্প্রয়োজন\n\n`;
            } else {
                if (hadith.type && hadith.type?.title) {
                    string += `তাহকীকঃ ${hadith.type.title}\n\n`;
                }
            }
        }
        if (this.rabi && hadith.rabis.length > 0) {
            string += `বর্ণনাকারীঃ `;
            for (const rabi of hadith.rabis) {
                string += ` ${rabi.title}`;
                if (rabi.death_year) {
                    string += `(মৃত্যুঃ ${this.helperService.changeEnglishToBengali(
                        rabi.death_year
                    )} হিজরী)`;
                }
                if (rabi.id !== hadith.rabis[hadith.rabis.length - 1].id) {
                    string += `,`;
                }
            }
            string += '\n\n';
        }
        if (this.website) {
            string += `হাদীসের লিংকঃ ${this.baseUrl}hadith/${hadith.id}\n`;
        }
        if (this.applink) {
            string += `অ্যাপসের লিংকঃ https://apps.apple.com/us/app/id878084956\n\n`;
        }
        try {
            textarea = document.createElement('textarea');
            textarea.setAttribute('readonly', true);
            textarea.setAttribute('contenteditable', true);
            textarea.style.position = 'fixed'; // prevent scroll from jumping to the bottom when focus is set.
            textarea.value = string;

            document.body.appendChild(textarea);

            textarea.focus();
            textarea.select();

            const range = document.createRange();
            range.selectNodeContents(textarea);

            const sel = window.getSelection();
            sel?.removeAllRanges();
            sel?.addRange(range);

            textarea.setSelectionRange(0, textarea.value.length);
            result = document.execCommand('copy');
        } catch (err) {
            console.error(err);
            result = null;
        } finally {
            document.body.removeChild(textarea);
        }

        // manual copy fallback using prompt
        if (!result) {
            const isMac = navigator.platform.toUpperCase().indexOf('MAC') >= 0;
            const copyHotkey = isMac ? '⌘C' : 'CTRL+C';
            result = prompt(`Press ${copyHotkey}`, string); // eslint-disable-line no-alert
            if (!result) {
                this.snackBarService.warn(`Sorry! Link not copied!`);
            }
        }
        this.snackBarService.success(`হাদীসটি কপি করা হয়েছে।`);
        this.closeDialog();
    }

    addHtmlTag(str: any): string {
        const htmlRemoved = str.replace(/&lt;p&gt;/g, '<p>');
        return htmlRemoved
            .replace(/&lt;\/p&gt;/g, '</p>')
            .replace(/&amp;#39;/g, '’')
            .replace(/&amp;rsquo;/g, '’')
            .replace(/&amp;lsquo;/g, '‘')
            .replace(/&amp;mdash;/g, '-')
            .replace(/&amp;zwnj;/g, '&zwnj;')
            .replace(/&amp;quot;/g, '″')
            .replace(/&lt;hr \/&gt;/g, '<hr/>')
            .replace(/&lt;br \/&gt;/g, '<br />')
            .replace(/&lt;strong&gt;/g, '<strong>')
            .replace(/&lt;\/strong&gt;/g, '</strong>')
            .replace(/&lt;p dir=&quot;rtl&quot;&gt;/g, '<p>')
            .replace(/&#039;/g, '’')
            .replace(/&rsquo;/g, '’')
            .replace(/&rlm;/g, '')
            .replace(/&quot;/g, '')
            .replace(/&amp;ldquo;/g, '“')
            .replace(/&amp;rdquo;/g, '”')
            .replace(/&ndash;/g, '–')
            .replace(/\(ইফাঃ\)/g, '')
            .replace(/&zwnj;/g, '');
    }

    openShareDialog(hadith: any): void {
        let hadithText = '';
        if (this.title_ar) {
          hadithText += `${HelperService.removeHtmlTag(
            this.addHtmlTag(this.newLine(hadith.title_ar))
          )}\n\n`;
        }
        if (this.hadith_ar) {
          hadithText += `${HelperService.removeHtmlTag(
            this.addHtmlTag(this.newLine(hadith.description_ar))
          )}\n\n`;
        }
        if (this.title_bn) {
          hadithText += `পরিচ্ছেদঃ ${HelperService.removeHtmlTag(
            this.addHtmlTag(this.newLine(hadith.title))
          )}\n\n`;
        }
        if (this.hadith_bn) {
          hadithText += `${HelperService.removeHtmlTag(
            this.addHtmlTag(this.newLine(hadith.description))
          )}\n\n`;
        }
        hadithText += `${hadith.book.title}\n`;
        // tslint:disable-next-line:triple-equals
        if (hadith.book_id == this.tohabiBookId ||
          // tslint:disable-next-line:triple-equals
          hadith.book_id == this.misqatBookId) {
          hadithText += `হাদীস নং: ${this.helperService.changeEnglishToBengali(
            hadith.hadith_number.toString()
          )}  `;
          if (hadith.hadith_number_one) {
            hadithText += ` - ${this.helperService.changeEnglishToBengali(
              hadith.hadith_number_one?.toString()
            )}`;
          }
        } else {
          hadithText += `হাদীস নং: ${this.helperService.changeEnglishToBengali(
            hadith.hadith_number.toString()
          )}  `;
          if (hadith.hadith_number_one) {
            hadithText += `আন্তর্জাতিক নং: ${this.helperService.changeEnglishToBengali(
              hadith.hadith_number_one.toString()
            )}`;
          }
          if (hadith.hadith_number_two) {
            hadithText += ` - ${this.helperService.changeEnglishToBengali(
              hadith.hadith_number_two.toString()
            )}`;
          }
        }
        hadithText += '\n\n';
        if (this.type) {
          if (hadith.book.id === 1 || hadith.book.id === 2) {
            hadithText += `তাহকীকঃ তাহকীক নিষ্প্রয়োজন\n\n`;
          } else {
            if (hadith.type && hadith.type?.title) {
              hadithText += `তাহকীকঃ ${hadith.type.title}\n\n`;
            }
          }
        }
        if (this.rabi && hadith.rabis.length > 0) {
          hadithText += `বর্ণনাকারীঃ `;
          for (const rabi of hadith.rabis) {
            hadithText += ` ${rabi.title}`;
            if (rabi.death_year) {
              hadithText += `(মৃত্যুঃ ${this.helperService.changeEnglishToBengali(
                rabi.death_year
              )} হিজরী)`;
            }
            if (rabi.id !== hadith.rabis[hadith.rabis.length - 1].id) {
              hadithText += `,`;
            }
          }
          hadithText += '\n\n';
        }
        if (this.website) {
          hadithText += `হাদীসের লিংকঃ ${this.baseUrl}hadith/${hadith.id}\n`;
        }
        if (this.applink) {
          hadithText += `অ্যাপসের লিংকঃ https://apps.apple.com/us/app/id878084956\n\n`;
        }

        this.closeDialog();
        // @ts-ignore
        AndroidNative.share(hadithText);
    }
}
