import { Component, Inject, OnInit } from '@angular/core';
import { SearchData } from '@app/models';
import { HelperService } from '@app/services';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import {
    MatDialog,
    MAT_DIALOG_DATA,
    MatDialogRef,
} from '@angular/material/dialog';

@Component({
    selector: 'app-search-comp',
    templateUrl: './search-comp.component.html',
    styleUrls: ['./search-comp.component.css'],
})
export class SearchCompComponent implements OnInit {
    searchForm!: FormGroup;
    submitted!: boolean;
    hadithNoPattern = new RegExp('^[\u09E6-\u09EF ]*$|^[0-9 ]*$');

    constructor(
        public dialog: MatDialog,
        private formBuilder: FormBuilder,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        public helperService: HelperService,
        public dialogRef: MatDialogRef<SearchCompComponent>,
        @Inject(MAT_DIALOG_DATA) public searchFormData: SearchData
    ) {
        this.createForm();
    }

    createForm() {
        this.searchForm = this.formBuilder.group({
            q: ['', [Validators.required, Validators.minLength(2)]],
            book: [''],
            hadith_no: ['', Validators.pattern(this.hadithNoPattern)],
        });
    }

    ngOnInit(): void {
        if (this.searchFormData.book) {
            this.searchForm.controls.book.patchValue(
                this.searchFormData.book.id
            );
            this.updateValidator();
        }
        this.activatedRoute.queryParams.subscribe((queryParams) => {
            if (queryParams.q) {
                this.searchForm.controls.q.patchValue(queryParams.q);
            }
        });
    }

    get getSearchForm() {
        return this.searchForm.controls;
    }

    removeHadithNoValidator() {
        if (this.searchForm.value.q) {
            this.searchForm.controls.book.setValidators([]);
            this.searchForm.controls.book.updateValueAndValidity();
            this.searchForm.controls.hadith_no.setValue('');
        }
        if (this.searchForm.value.book && this.searchForm.value.q) {
            this.searchForm.controls.hadith_no.setValidators([]);
            this.searchForm.controls.hadith_no.updateValueAndValidity();
        }
    }

    removeValidator() {
        this.searchForm.controls.q.setValidators([]);
        this.searchForm.controls.q.updateValueAndValidity();
        this.searchForm.controls.hadith_no.setValidators([]);
        this.searchForm.controls.hadith_no.updateValueAndValidity();
    }

    updateValidator() {
        if (!this.searchForm.value.q && this.searchForm.value.book) {
            this.removeValidator();
            this.searchForm.controls.hadith_no.setValidators([
                Validators.required,
            ]);
            this.searchForm.controls.hadith_no.updateValueAndValidity();
        } else {
            this.removeValidator();
        }
    }

    addBookValidator() {
        this.searchForm.controls.book.setValidators([Validators.required]);
        this.searchForm.controls.book.updateValueAndValidity();
        this.searchForm.controls.q.setValidators([]);
        this.searchForm.controls.q.updateValueAndValidity();
        if (this.searchForm.value.hadith_no) {
            this.searchForm.controls.q.setValue('');
        }
    }

    searchFormSubmit(): void {
        this.submitted = true;
        if (this.searchForm.invalid) {
            return;
        }
        const queryParams = {
            q: this.searchForm.value.q.trim(),
            book: this.searchForm.value.book,
            hadith_no: this.helperService.changeBengaliToEnglish(
                this.searchForm.value.hadith_no.trim()
            ),
        };
        this.router.navigate(['/hadith-mobile/search'], {
            queryParams,
        });
        this.closeDialog();
    }

    closeDialog(): void {
        this.dialogRef.close();
    }
}
