import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '@environments/environment';
import { BookData, ChapterData, TypeData } from '@app/models';

@Injectable({
  providedIn: 'root',
})
export class HadithService {
  constructor(private http: HttpClient) {}

  getHadith(hadithId): Observable<any> {
    const httpHeaders: HttpHeaders = new HttpHeaders({ skip: 'true' });
    return this.http.get<any>(`${environment.hadithApiUrl}hadith/${hadithId}`, {
      headers: httpHeaders,
    });
  }

  getBooks(): Observable<BookData> {
    const httpHeaders: HttpHeaders = new HttpHeaders({ skip: 'true' });
    return this.http.get<BookData>(
      `${environment.hadithApiUrl}books?show=all&order=sequence&direction=asc`,
      { headers: httpHeaders }
    );
  }

  getDownloadBooks(): Observable<BookData> {
    const httpHeaders: HttpHeaders = new HttpHeaders({ skip: 'true' });
    return this.http.get<BookData>(
      `${environment.hadithDownloadUrl}/books?key=263fjuU3rkQ`,
      { headers: httpHeaders }
    );
  }

  downloadChaptersBySlug(slug: string) {
    const httpHeaders: HttpHeaders = new HttpHeaders({ skip: 'true' });
    return this.http.get<BookData>(
      `${environment.hadithDownloadUrl}chapters/${slug}?key=263fjuU3rkQ`,
      { headers: httpHeaders }
    );
  }

  downloadTitlesBySlug(slug: string) {
    const httpHeaders: HttpHeaders = new HttpHeaders({ skip: 'true' });
    return this.http.get<BookData>(
      `${environment.hadithDownloadUrl}titles/${slug}?key=263fjuU3rkQ`,
      { headers: httpHeaders }
    );
  }

  downloadHadithsBySlug(slug: string) {
    const httpHeaders: HttpHeaders = new HttpHeaders({ skip: 'true' });
    return this.http.get<BookData>(
      `${environment.hadithDownloadUrl}hadiths/${slug}?key=263fjuU3rkQ`,
      { headers: httpHeaders }
    );
  }

  // tslint:disable-next-line:typedef
  getChaptersByBookId(bookId): Observable<ChapterData> {
    const httpHeaders: HttpHeaders = new HttpHeaders({ skip: 'true' });
    // tslint:disable-next-line:max-line-length
    return this.http.get<ChapterData>(
      `${environment.hadithApiUrl}book/${bookId}/chapters?show=all&order=sequence`,
      { headers: httpHeaders }
    );
  }

  getAllTitlesByBookAndChapter(
    bookId: any,
    chapterId: any
  ): Observable<TypeData> {
    const httpHeaders: HttpHeaders = new HttpHeaders({ skip: 'true' });
    return this.http.get<TypeData>(
      `${environment.hadithApiUrl}book/${bookId}/chapter/${chapterId}/titles`,
      {
        headers: httpHeaders,
      }
    );
  }
}
