import { Component, OnInit } from '@angular/core';
import { HelperService } from '@app/services';

import { Sort } from '@angular/material/sort';

@Component({
  selector: 'app-bookmark-hadith',
  templateUrl: './bookmark-hadith.component.html',
  styleUrls: ['./bookmark-hadith.component.css'],
})
export class BookmarkHadithComponent implements OnInit {
  hadiths: any[] = [];
  footerMenus: any[] = [
    {
      id: 1,
      title: 'হোম',
      img: 'home.svg',
      slug: 'home',
      link: '/',
    },
    {
      id: 4,
      title: 'খুঁজুন',
      img: 'search.svg',
      slug: 'search',
      link: '',
    },
    {
      id: 3,
      title: 'পিছনে',
      img: 'back.svg',
      slug: 'back',
      link: '/',
    },
  ];
  constructor(public helperService: HelperService) {
    Window['myComponent'] = this;
  }
  ngOnInit(): void {
    this.getHadiths();
  }

  getHadiths(options: any = {}, sort?: Sort): void {
    let hadiths = localStorage.getItem('hadiths');
    if (hadiths?.length) {
      this.hadiths = JSON.parse(hadiths);
    }
  }
}
