import { Component, OnInit } from '@angular/core';
import { Hadith } from '@app/models';
import { HelperService } from '@app/services';

@Component({
  selector: 'app-last-read-hadith',
  templateUrl: './last-read-hadith.component.html',
  styleUrls: ['./last-read-hadith.component.css'],
})
export class LastReadHadithComponent implements OnInit {
  arabicText = true;
  hadith!: Hadith;
  footerMenus: any[] = [
    {
      id: 1,
      title: 'হোম',
      img: 'home.svg',
      slug: 'home',
      link: '/',
    },
    {
      id: 4,
      title: 'খুঁজুন',
      img: 'search.svg',
      slug: 'search',
      link: '',
    },
    {
      id: 3,
      title: 'পিছনে',
      img: 'back.svg',
      slug: 'back',
      link: '/',
    },
  ];

  constructor(public helperService: HelperService) {
    Window['myComponent'] = this;
  }

  ngOnInit(): void {
    this.getHadith();
  }

  getHadith(): void {
    const hadith = localStorage.getItem('hadith');
    if (hadith) {
      this.hadith = JSON.parse(hadith);
    }
  }
}
