import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SearchCompComponent } from '@app/components/component/search-comp/search-comp.component';
import { MatDialog } from '@angular/material/dialog';
import { Book } from '@app/models';
import { NoopScrollStrategy } from '@angular/cdk/overlay';
import { IndexeddbService } from '@app/services/common/indexeddb.service';
import { HelperService } from '@app/services';
import { Location } from '@angular/common';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css'],
})
export class FooterComponent implements OnInit {
  @Input() footerMenus: any;
  books: Book[] = [];
  constructor(
    private router: Router,
    public dialog: MatDialog,
    private helperService: HelperService,
    private indexeddbService: IndexeddbService,
    private location: Location
  ) {}

  ngOnInit(): void {
    this.getBooks();
  }

  onclickMenu(menu: any): void {
    if (menu.slug === 'search') {
      if (menu.hasOwnProperty('book')) {
        this.openSearchDialog(menu.book);
      } else {
        this.openSearchDialog();
      }
    } else if (menu.slug === 'close') {
      this.closeApp();
    } else if (menu.slug === 'goBack') {
      this.goBack();
    } else {
      this.router.navigate([menu.link]);
    }
  }

  openSearchDialog(book?: Book): void {
    const config = {
      data: {
        books: this.books
          .sort(this.helperService.sortBy('sequence'))
          .filter((value: Book) => {
            return value.downloaded;
          }),
        book,
      },
      width: '350px',
      autoFocus: false,
      scrollStrategy: new NoopScrollStrategy(),
    };
    this.dialog.open(SearchCompComponent, config);
  }

  getBooks(): void {
    this.indexeddbService.getAll('books').subscribe((item) => {
      this.books = item;
    });
  }

  closeApp(): void {
    // @ts-ignore
    AndroidNative.finish();
  }

  goBack(): void {
    this.location.back();
  }
}
